import { Button, Checkbox, Form, Input, message, Switch } from "antd"
import React from "react"
import { IMovie } from "../services/MovieService"
import { ImgUploader } from "./ImgUploader"
import { NavigateFunction, useNavigate } from "react-router"
import { FormInstance } from "antd"


const allAreas: {label: string, value: string}[] = [
    {label: "", value: ""},
    {label: "", value: ""},
    {label: "", value: ""},
    {label: "", value: ""},
    {label: "", value: ""}
]

const allTypes: {label: string, value: string}[] = [
    {label: "", value: ""},
    {label: "", value: ""},
    {label: "", value: ""},
    {label: "", value: ""},
    {label: "", value: ""}
]

interface IFormProp {
    /**
     * 
     */
    onSubmit: (movie: IMovie) => Promise<string>
    /**
     * 
     */
    navigate?: NavigateFunction
    /**
     * Form
     */
    ref1?: React.ForwardedRef<FormInstance<any>>
}

class MovieForm extends React.Component<IFormProp> {
    /**
     * 
     */
    private async finished(value: any) {
        const result = await this.props.onSubmit(value as IMovie)
        if(result) {
            message.error(result)
        } else {
            message.success("");
            // 
            this.props.navigate && this.props.navigate(-1)
        }
    }

    render() {
        return (
            <Form
                name="basic"
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 8 }}
                autoComplete="off"
                onFinish={this.finished.bind(this)}
                ref={this.props.ref1}
            >
                <Form.Item
                    label=""
                    name="name"
                    rules={[{ required: true, message: '!' }]}
                >
                    <Input />
                </Form.Item>

                <Form.Item label="" name="types" wrapperCol={{ offset: 0, span: 16 }} rules={[{required: true, message: ""}]}>
                    <Checkbox.Group 
                        options={allTypes}
                    />
                </Form.Item>

                <Form.Item
                    label=""
                    name="area"
                    wrapperCol={{ offset: 0, span: 16 }}
                    rules={[{ required: true, message: '!' }]}
                >
                    <Checkbox.Group 
                        options={allAreas}
                    />
                </Form.Item>

                <Form.Item
                    label=" ()"
                    name="timeLong"
                    rules={[{required: true, message: ""}, 
                    {pattern: /^([1-9]\d{0,2})$/, message: "1~999"}]}
                >
                    <Input type="number" min={1} max={999}/>

                </Form.Item>

                <Form.Item 
                    label=""
                    name="isHot"
                    initialValue={false}
                    valuePropName="checked"
                >
                    <Switch />
                </Form.Item>

                <Form.Item 
                    label=""
                    name="isComing"
                    initialValue={false}
                    valuePropName="checked"
                >
                    <Switch />
                </Form.Item>

                <Form.Item 
                    label=""
                    name="isClassic"
                    initialValue={false}
                    valuePropName="checked"
                >
                    <Switch />
                </Form.Item>

                <Form.Item 
                    label=""
                    name="description"
                >
                    <Input.TextArea />
                </Form.Item>

                <Form.Item
                    label=""
                    name="poster"
                >
                    <ImgUploader />
                </Form.Item>

                <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                    <Button type="primary" htmlType="submit">
                        
                    </Button>
                    <Button htmlType="button" 
                        onClick={() => {
                            this.props.navigate && this.props.navigate(-1)
                        }}
                        style={{
                            marginLeft: 50
                        }}>
                        
                    </Button>
                </Form.Item>
            </Form>
        )
    }
}

// navigate
// ref
const MovieFormWrapper = React.forwardRef<FormInstance, IFormProp>((props, ref)=>{
    return <MovieForm {...props} ref1={ref} navigate={useNavigate()}/>
  })

export default MovieFormWrapper